<template>
  <div class="chatTime">   

    <span>
      <date v-if="time" :date="time"/>
      <span class="new" v-else>New</span>
    </span>
    
  </div>
</template>

<style scoped lang="sass">

.new
  color : $color-good
</style>

<script>


export default {
  name: 'chatTime',

  props : {
    chat : Object
  },

  computed:{
    time : function(){
      var d = this.chat?.getLastActiveTimestamp() || -1

      if (d < 0) return null

      return d
    }
  }
}
</script>



