import { mapState } from 'vuex';

import contacts from '@/components/contacts/index.vue'

export default {
    name: 'chatsTopheader',
    props: {
    },

    components : {
        contacts,
    },

    data : function(){

        return {
            loading : false,
            newchatopened : false,
            createGroup: false,
            contacts: false,
            startMode: 'GroupsCreate'
        }

    },

    created(){

        this.$store.commit('SET_CURRENT_ROOM', 'all')
         
    },

    destroyed(){

        this.$store.commit('SET_CURRENT_ROOM', false)
         
    },

    watch: {
        //$route: 'getdata'
    },

    beforeRouteEnter (to, from, next) {
        next()
    },

    beforeRouteUpdate (to, from, next) {
        next()
    },

    computed: mapState({
        auth : state => state.auth,
    }),

    methods : {
        newchatmenu : function(){
            this.newchatopened = !this.newchatopened
        },

        newchat : function(contact){
            this.newchatopened = false

            var id = this.core.mtrx.kit.tetatetid(contact, this.core.user.userinfo)

            setTimeout(() => {
                this.$router.push('chat?id=' + id + '&u=' + contact.id)
            }, 400)
            
        },
    },
}