import { mapState } from 'vuex';
import chatName from '@/components/chats/assets/name.vue'
import chatTime from '@/components/chats/assets/time.vue'
import chatIcon from '@/components/chats/assets/icon.vue'

import event from '@/components/events/event/index.vue'

export default {
    name: 'chatsPreview',
    props: {
        chat : Object
    },

    components : {
        chatName,
        chatTime,
        chatIcon,

        event
    },

    data : function(){

        return {
            loading : false
        }

    },

    computed: mapState({
        auth : state => state.auth,

        m_chat : function(){

            if (this.chat.roomId){

                var m_chat = this.core.mtrx.client.getRoom(this.chat.roomId)


                if(m_chat){
                    //console.log(m_chat, m_chat.getAvatarUrl(), m_chat.currentState.getStateEvents('m.room.member'))
                }

                //console.log(this.core.mtrx.client.getRoom(this.chat.roomId))
                return m_chat || {}
            }
                
        },

        event : function(){
            if (this.chat.roomId){
                var events = ((this.$store.state.events[this.chat.roomId] || {}).timeline || [])

             

                if (events.length){
                    return events[events.length - 1]
                }
            }
            
        }
    }),

    methods : {
        
    },
}