import {mapActions, mapState} from 'vuex'
import preview from '@/components/chats/preview/index.vue'
import {SwipeList, SwipeOut} from 'vue-swipe-actions';
import 'vue-swipe-actions/dist/vue-swipe-actions.css';
import userspic from '@/components/assets/userspic/index.vue'

export default {
  name: "chatsList",
  data: function () {

    return {
      loading: false,
      textValue: '',
      enabled: true,
      page: 0,
      revealed: {},
      lastEventDescription: '',
    }

  },
  components: {
    preview,
    SwipeOut,
    SwipeList,
    userspic
  },

  props: {
    user_data: {
      type: String,
      default: () => {
      }
    }
  },
  created: () => {

  },

  watch: {
    //$route: 'getdata'
  },

  beforeRouteEnter(to, from, next) {
    next()
  },

  beforeRouteUpdate(to, from, next) {
    next()
  },
  beforeDestroy() {
    window.removeEventListener('keydown', this.onKeyDown);
    window.removeEventListener('keyup', this.onKeyUp);
  },

  computed: mapState({
    auth: state => state.auth,

    ...mapState([
      'chats'
    ]),

  }),

  methods: {
    dd(v) {
      console.log(v.target);
    },
    revealFirstRight() {
      this.$refs.list.revealRight(0);
    },
    revealFirstLeft() {
      this.$refs.list.revealLeft(0);
    },
    // closeFirst() {
    //   this.$refs.list.closeActions(0);
    // },
    // closeAll() {
    //   this.$refs.list.closeActions();
    // },
    remove(item) {
      this.$set(this.mockSwipeList, this.page, this.mockSwipeList[this.page].filter(i => i !== item));
    },
    setLastEvent(name, {item, index}) {
      this.lastEventDescription = {
        name,
        index,
        id: item.id,
      };
    },
    itemClick(e) {
      console.log(e, 'item click');
    },
    fbClick(e) {
      console.log(e, 'First Button Click');
    },
    sbClick(e) {
      console.log(e, 'Second Button Click');
    },
    // keyboard
    onKeyDown(e) {
      if (e.keyCode !== 16) return;
      this.enabled = false;
    },
    onKeyUp(e) {
      if (e.keyCode !== 16) return;
      this.enabled = true;
    },
    removeRoom(roomId) {
      this.core.mtrx.client.getJoinedRoomMembers(roomId).then(users => {
        console.log(users.joined)
        for (var key in users.joined) {
          console.log(key)
          this.core.mtrx.client.kick(roomId, key, 'leave').then(result=>{
            console.log(result, "asdasdasds")
          })
        }
        this.core.mtrx.client.forget(roomId, true)
      })
      // this.core.mtrx.client.kick(roomId, "@5054637a384444505751513465526f4166656674455632367744316f7a5233387944:matrix.pocketnet.app", 'leave' )
      // this.core.mtrx.client.store.removeRoom(roomId)
      // this.core.mtrx.client.setPowerLevel(roomId, "@504b737a766a48774d577562517065454b6e5168526f6e4742564657634a44624e4a:matrix.pocketnet.app", 100)
      console.log(this.core.mtrx)
    }
  },
  mounted() {
    // ideally should be in some global handler/store
    window.addEventListener('keydown', this.onKeyDown);
    window.addEventListener('keyup', this.onKeyUp);
  }
}